@media (max-width: 1024px) { .footer-info { font-size: 10px; }
  .footer-info br { display: none; } }

.footer-links { display: flex; flex-direction: column; margin-top: 20px; }

@media (max-width: 340px) { .footer-links { margin: 0; } }

.footer-links.catalog a { color: #0D19A3; }

@media (max-width: 1024px) { .footer-links.catalog a { margin-bottom: 10px; font-size: 12px; } }

.footer-links a { margin-bottom: 18px; font-size: 18px; font-weight: 500; text-decoration: underline; }

.footer-links a:hover { opacity: .35; }

* { margin: 0; padding: 0; box-sizing: border-box; }

html { font-size: 16px; height: 100%; min-height: 100%; -webkit-text-size-adjust: 100%; scroll-behavior: smooth; }

body { height: 100%; min-height: 100%; font-family: 'Montserrat', sans-serif; font-weight: 400; font-size: 1rem; color: #000; background-color: #fff; }

#wrapper { display: flex; flex-direction: column; margin-left: auto; margin-right: auto; position: relative; overflow: hidden; min-height: 100%; min-width: 100%; }

#middle { -webkit-box-flex: 1; flex: 1 0 auto; }

#footer { -webkit-box-flex: 0; flex: 0 0 auto; }

.container { width: 100%; max-width: 1180px; margin: 0 auto; padding-left: 8px; padding-right: 8px; }
